import React from 'react';
import { useGoogleLogin } from 'react-google-login';

// refresh token
import refreshTokenSetup from './refreshTokenSetup.js';

const clientId =
  '1056983227630-7vnetvr9bq8folhj4vnns367o7ebok2s.apps.googleusercontent.com';

function LoginButton(props) {
  const onSuccess = (res) => {
    console.log('Login Success: currentUser:', res.profileObj);
    console.log('Hello ',  props.user);
    props.setUser(res.profileObj);
    alert(
      `Logged in successfully welcome ${res.profileObj.name} 😍. \n See console for full profile object.`
    );
    refreshTokenSetup(res);
  };

  const onFailure = (res) => {
    console.log('Login failed: res:', res);
    alert(
      `Failed to login. 😢`
    );
  };

  const { signIn } = useGoogleLogin({
    onSuccess,
    onFailure,
    clientId,
    isSignedIn: true,
    accessType: 'offline',
    // responseType: 'code',
    // prompt: 'consent',
  });

  return (
    <button onClick={signIn} className="button">
      <span className="buttonText">{props.user.name} Sign in with Google</span>
    </button>
  );
}

export default LoginButton;