import TextField from '@mui/material/TextField';

function Typebox({ setMessages }) {

    function addMessage(message) {
      console.log(message)
      const addUser = ['Sotaro', message]
      setMessages(prevmessages => [...prevmessages, addUser]);
    }

    return (
        <TextField
        variant="outlined"
        margin="dense"
        onKeyPress={(ev) => {
            if (ev.key === 'Enter') {
                ev.preventDefault();
                addMessage(ev.target.value);
            }
        }}>
        Send a message!(Typebox)
    </TextField>
    );
  }

export default Typebox;