import { VariableSizeList as List } from 'react-window';
import { useEffect } from 'react';
import React from 'react';
 


function Row(props) {
  const message = props.data[props.index];
  return (
    <div style={props.style}> {message[0]  + ': ' + message[1]} </div>
  );
};

function Chatlog(props) {

  const listRef = React.createRef();

  useEffect(() => {
    listRef.current.scrollToItem(props.messages.length)
  });
  
  return (
    <List ref={listRef}
  height={500}
  itemCount={props.messages.length}
  itemSize={()=>50}
  width={300}
  itemData={props.messages}
  initialScrollOffset={props.messages.length * 50}
  s
>
  {Row}
</List>
  );
}

export default Chatlog;